@import url(https://fonts.googleapis.com/css?family=Righteous&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  /* text-align: center; */
  display: grid;
  grid-template-rows: 30px 4fr 1fr;
  background-color: #e3e3e3;
  overflow: hidden;
}
.home {
  background: -webkit-gradient(
      linear,
      left bottom, left top,
      color-stop(3%, rgba(198, 0, 33, 0.31976540616246496)),
      color-stop(67%, rgba(255, 255, 255, 0.29175420168067223))
    ),
    url(/IBASheduler/static/media/IBA-33.efe995d1.png);
  background: linear-gradient(
      0deg,
      rgba(198, 0, 33, 0.31976540616246496) 3%,
      rgba(255, 255, 255, 0.29175420168067223) 67%
    ),
    url(/IBASheduler/static/media/IBA-33.efe995d1.png);
  background-size: cover; /* <------ */
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  /* border: 2px solid green; */
}

.home-text {
  /* border: 2px solid red; */
  font-size: 160%;
  text-align: center;
  margin-top: 170px;
  text-transform: uppercase;
  font-family: "Righteous", cursive;
}
.home-buttons {
  /* border: 2px solid yellow; */
  font-size: 180%;

  text-align: center;
}

.App-header {
  font-size: calc(10px + 2vmin);

  color: white;
  display: grid;
  justify-items: center;
}

.cardSheduler {
  display: grid;
  justify-content: center;
  align-items: center;
}

.navbar-links {
}
#navbar-drawer {
  display: none;
  border: 2px solid white;
  border-radius: 10%;
}
.sidebar-checkbox {
  position: absolute;
  z-index: 1;
  opacity: 0;
}
.sidebar {
  position: fixed;
  right: 0;
  width: 50%;
  z-index: -1;
  background-color: #c60021;
  height: 100%;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-transform: translateX(200px);
          transform: translateX(200px);

  /* margin-top: 30px; */
}
.sidebar-items {
  margin-top: 55%;
  list-style: none;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
}

.sidebar-item {
  color: white;
  text-transform: uppercase;
  border-bottom: 1px solid white;
  padding: 6px;
  margin: 5px;
  font-size: 15px;
}

#navi-toggle:checked ~ .sidebar {
  /* background-color: gainsboro; */
  /* transform: scale(80); */
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
}

/* Smartphones (portrait) ----------- */
@media only screen and (max-width: 450px) {
  /* Styles */
  .navbar-links {
    display: none;
  }
  #navbar-drawer {
    display: inline-block;
  }
}

